.App {
  text-align: center;
}

footer {
  margin: 0 auto;
  width: 50%;
  bottom: 0;
  left: 25%;
}
hr {
  border: 0.1px solid #ccc;
}

.header {
  height: 25%; 
  color: #3F51B5;
  /* padding: 10% 10% 0 10%; */
  text-align: center;
}


@media only screen and (max-width: 640px) {
  .header {
    height: 50%;
  }
  .profile h1{
    font-size: 2em;
  }
  footer {
    width: 100%;
    bottom: 0;
    left: 0;
  }
}

.profile h1{
  font-size: 2.5em;
}

.side-nav {
  right: 8px;
  position: absolute;
  top: 10px;
}

.side-nav a {
  padding: 10px;
  cursor: pointer;
  text-decoration: none;
  color: black;
}
.side-nav a:hover {
  background-color: #3F51B5;
  color: white;
}

section {
  text-align: center;
  margin: 5%;
}

li {
  text-align: left;
  margin-left:40%;
}


